import {apiPut, apiGet, apiPost, apiDelete} from "../../assets/js/baseApi";


let resetPasswd = function(username, oldPwd,  newPwd){
    return new Promise((resolve, reject) => {
        let data = {
            name: username,
            oldpasswd: oldPwd,
            newpasswd: newPwd
        }
        apiPut('/api/usermgr/resetPasswd', data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let addUserApi = function(form){
    return new Promise((resolve, reject) => {
        let data = {
            name: form.username,
            password: form.password,
            role: form.addRole
        }
        apiPost('/api/usermgr', data).then((data) => {
            resolve(data)
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

let deleteUserApi = function(username){
    return new Promise((resolve, reject) => {

        apiDelete('/api/usermgr/'+username).then((data) => {
            resolve(data)
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

let getAllUserApi = function(){
    return new Promise((resolve, reject) => {
        apiGet('/api/usermgr').then((data) => {
            resolve(data)
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

let getAllUserByPageApi = function(currentPage, limit){
    const data = {
        currentpage: currentPage,
        limit: limit
    }
    return new Promise((resolve, reject) => {
        apiGet('/api/usermgr/byPage', data).then((data) => {
            resolve(data)
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

let getAllUserByPageAndKeywordsApi = function(keywords, currentPage, limit){
    const data = {
        keywords: keywords,
        currentpage: currentPage,
        limit: limit
    }
    return new Promise((resolve, reject) => {
        apiGet('/api/usermgr/byPageAndKeywords', data).then((data) => {
            resolve(data)
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

let getUserByNameApi = function(username){
    let data = {
        name: username
    }
    return new Promise((resolve, reject) => {

        apiGet('/api/usermgr/byname', data).then((data) => {
            resolve(data)
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

let updateUserByNameApi = function(username, role){
    let data = {
        name: username,
        role: role
    }
    return new Promise((resolve, reject) => {

        apiPost('/api/usermgr/byname', data).then((data) => {
            resolve(data)
        }).catch((error) => {
            resolve(err.response)
        })
    })
}

export {
    resetPasswd,
    getAllUserApi,
    deleteUserApi,
    getUserByNameApi,
    addUserApi,
    updateUserByNameApi,
    getAllUserByPageApi,
    getAllUserByPageAndKeywordsApi
}


