<template>
	<div class="m-l-10">
        <div class="m-t-10 ">
            <p class="header-line"><i class="fa fa-sliders c-blue m-r-10" aria-hidden="true"></i>
            {{$t('user.userList')}}
            </p>
        </div>
		<div class="cf panel-header">
            <div class="fr m-r-10"> 
                <!-- <el-button size="small" type="success" @click="dialogAddUserVisible=true">Add</el-button>  -->
                <el-input size="small" class="w-300 m-l-10" v-model="keywords" :placeholder="$t('user.keyword')" @keyup.enter.native="searchUser">
                    <el-button slot="append" icon="el-icon-search"  @click="searchUser" ></el-button>
                </el-input>
            </div>
        </div>
		<el-table
		:data="tableData"
		style="width: 100%">
            <el-table-column
            label=""
            width="60"
            >
                <template slot-scope="scope">
                    <div>
                        <i class="fa fa-user c-primary" style="fontSize: 21px" ></i>
                    </div>
                </template>
            </el-table-column>
            <el-table-column
			:label="$t('user.userName')"
			prop="name"
			min-width="80">
			</el-table-column>

			<el-table-column
			:label="$t('user.create')"
			min-width="150">
                <template slot-scope="scope">
                    <div>
                        {{ scope.row.ts|time}}
                    </div>
                </template>
			</el-table-column>
      
			<el-table-column
			:label="$t('user.role')"
			min-width="150">
                <template slot-scope="scope">
                    <div>
                        {{ scope.row.role|roleFunc}} 
                    </div>
                </template>
			</el-table-column>
             <el-table-column
                :label="$t('global.action')"
                min-width="165"
                align="center">
                <template slot-scope="scope">
                    <el-button 
                        size="mini" 
                        type="danger"
                        @click="deleteUser(scope.row.name)"
                        :disabled="scope.row.role==0|| scope.row.role==1"
                        >
                        {{$t('global.delete')}}
                    </el-button>
                </template>
            </el-table-column>
		</el-table>
   
		<div class="p-t-20 fr">
       
			<div>
				<el-pagination
				@current-change="handleCurrentChange"
				layout="prev, pager, next"
				:page-size="limit"
				:current-page="currentPage"
				:total="count"
                 v-show="isshow">
				</el-pagination>
			</div>
		</div>
         <el-button  
            size="small" 
            @click="dialogAddUserVisible=true" 
            type="primary" 
            class="fl m-t-10 cf m-l-10 m-b-20">
            {{$t('global.add')}}
        </el-button>
        <el-dialog :visible.sync="dialogAddUserVisible" @close="closeAddDialog">
             <div slot="title" class="dialog-title">
                <i class="fa fa-plus-square-o m-r-10" aria-hidden="true"></i>
                {{$t('user.addUser')}}
            </div>
            <el-form ref="form" :model="form" :rules="rules" label-width="130px" class="m-l-30">
                <el-form-item :label="$t('label.email')" :label-width="formLabelWidth" prop="username" >
                    <el-input v-model="form.username" autocomplete="off" style="width: 320px"ã€€:placeholder="$t('user.emailhold')"></el-input>
                </el-form-item>
            </el-form>
            
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddUserVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="addUser()" v-loading="isLoading">{{$t('global.add')}}</el-button>
            </div>
        </el-dialog>
	</div>
</template>

<script>
    import {
        getAllUserApi,
        deleteUserApi, 
        addUserApi, 
        getAllUserByPageApi, 
        getAllUserByPageAndKeywordsApi, 
        getUserByNameApi,
        updateUserByNameApi
        } from "../restfulapi/userInfo"
    import handleResponse from "../restfulapi/handleResponse"

    export default {
        data() {
            let validateLogin = (rule,value,callback)=>{
                let reg=new RegExp(/^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyEmailError')))
                }else{callback()};
            };
            return {
                tableData: [],
                count: -1,
                currentPage: 1,
                keywords: '',
                realKeywords: '',
                multipleSelection: [],
                limit: 10,
                dialogAddUserVisible:false,
                dialogEditUserRoleVisible: false,
                formLabelWidth: "130px",
                isshow: false,

                form: {
                    username: '',

                },
                rules: {
                    username: {validator: validateLogin, trigger: 'blur'},
                },
                isLoading: false
            }
        },
        methods: {

            handleCurrentChange(currentPage) {
                if(currentPage){
                    this.currentPage = currentPage;
                }else{
                    this.currentPage = 1
                }
                if(this.realKeywords){
                    this.getUserLikeName(this.realKeywords);
                }else{
                    this.getAllUsers(); 
                }
            },

            deleteUser(uname) {
                _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        deleteUserApi(uname).then((res) => {
                            handleResponse(res, (data) => {
                                if(data.status === "CHANGED"){
                                    this.$swal("",this.$t('global.success'),"success", {button: this.$t('global.confirm')}).then(() => {
                                        this.getAllUsers();
                                    })
                                }
                                
                            })
                        })
                    }
                })
               
            },

            getAllUsers() {
                getAllUserByPageApi(this.currentPage, this.limit).then((res) => {
                    handleResponse(res, (data) => {
                        if(_g.isObject(data)){
                            this.tableData = data.data;
                            this.count = data.count;
                            this.isshow = this.count > this.limit;
                        }
                    })
                })
            },

            searchUser(){
                this.realKeywords = this.keywords
                this.currentPage = 1;
                this.getUserLikeName(this.realKeywords);
            },

            getUserLikeName(keyword){
                getAllUserByPageAndKeywordsApi(this.keywords, this.currentPage, this.limit).then((res) => {
                    handleResponse(res, (data) => {
                        if(_g.isObject(data)){
                            this.tableData = data.data;
                            this.count = data.count;
                            this.isshow = this.count > this.limit;
                        }
                    })
                })
            },

           addUser() {
               this.$refs.form.validate((valid) => {
                   if(valid){
                       this.isLoading = true;
                        addUserApi(this.form).then((res) => {
                            handleResponse(res, (data) => {
                                this.isLoading = false;
                                if(data.status === "CHANGED"){
                                    this.$swal("",this.$t('global.success'),"success", {button: this.$t('global.confirm')}).then(() => {
                                        this.getAllUsers();
                                    })
                                }else{
                                    _g.handleError(data);
                                }
                                this.dialogAddUserVisible = false;
                            })
                        })
                    }
               })
            },

            init() {
                this.getAllUsers()
            },

           

            
            closeAddDialog(){
                this.form =  {
                    username: '',
                    password: '',
                    addRole: "1"
                }
            },
            
        },
        created() {
            this.init()
        },
        watch: {
            '$route' (to, from) {
                this.init()
            }
        },
    }
</script>


